// Font weight
$font-weight-medium: 500;
$font-weight-semibold: 600;

// stylelint-disable value-keyword-case
$font-family-sans-serif: 'Nunito Sans', sans-serif;
$font-family-monospace: SFMono-Regular,
Menlo,
